import Toastify from "toastify-js/src/toastify-es.js"
import CableReady from "cable_ready"

CableReady.operations.stimulusReflexVersionMismatch = operation => {
  const levels = {
    "info": {},
    "success": { background: "rgb(0, 176, 155)" },
    "warn": { background: "rgb(247, 187, 60)" },
    "error": { background: "rgb(255, 95, 109)" },
  }

  const defaults = {
    selector: setupToastify(),
    close: true,
    duration: 30 * 1000,
    gravity: "bottom",
    position: "right",
    newWindow: true,
    style: levels[operation.level || "info"]
  }

  Toastify({ ...defaults, ...operation }).showToast()
}

function setupToastify() {
  const id = "stimulus-reflex-toast-element"
  let element = document.querySelector(`#${id}`)

  if (!element) {
    element = document.createElement("div")
    element.id = id
    document.documentElement.appendChild(element)

    const styles = document.createElement("style")
    styles.innerHTML = `
      #${id} .toastify {
         padding: 12px 20px;
         color: #ffffff;
         display: inline-block;
         box-shadow: 0 3px 6px -1px rgba(0, 0, 0, 0.12), 0 10px 36px -4px rgba(77, 96, 232, 0.3);
         background: -webkit-linear-gradient(315deg, #73a5ff, #5477f5);
         background: linear-gradient(135deg, #73a5ff, #5477f5);
         position: fixed;
         opacity: 0;
         transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
         border-radius: 2px;
         cursor: pointer;
         text-decoration: none;
         max-width: calc(50% - 20px);
         z-index: 2147483647;
         bottom: -150px;
         right: 15px;
      }

      #${id} .toastify.on {
        opacity: 1;
      }

      #${id} .toast-close {
        background: transparent;
        border: 0;
        color: white;
        cursor: pointer;
        font-family: inherit;
        font-size: 1em;
        opacity: 0.4;
        padding: 0 5px;
      }
    `

    document.head.appendChild(styles)
  }

  return element
}
